import React, { useState } from 'react';
import { arrayOf, shape, string } from 'prop-types';
import Link from 'gatsby-link';
import Dropdown from 'react-dropdown';
import styled from 'styled-components';
import 'react-dropdown/style.css';

const Article = styled.article`
  margin: 2em 0;
`;

const PageTitle = styled.h1`
  font-size: 32px;
`;

const Title = styled.h4`
  font-size: 20px;
`;

const Content = styled.section`
  margin: 1em 0 0;
`;

const Frame = styled.div`
  width: 320px;
  height: 240px;
  background-image: url('${({ image }) => image}');
  background-position: center;
  backgorund-size: cover;
`;

const StyledDropdown = styled(Dropdown)`
  width: 320px;
`;

const List = ({ posts }) => {
  const [category, setCategory] = useState(null);
  const options = Object.values(
    posts
      .map(({ node }) => node.categories)
      .flat()
      .reduce(
        (acc, curr) => Object.assign(acc, { [curr.id]: { label: curr.name, value: curr.id } }),
        {}
      )
  );
  console.log({ options });

  return (
    <>
      <PageTitle>
        Article List
      </PageTitle>
      <StyledDropdown
        options={options}
        onChange={setCategory}
        value={category}
        placeholder="Select an option"
      />
      {
        posts
          .filter(({ node }) => {
            if (category === null) {
              return true;
            }

            console.log(node.categories, category.value);

            if (node.categories.map(({ id }) => id).includes(category.value)) {
              return true;
            }

            return false;
          })
          .map(({ node }) => {
            const { id, title, slug, content, preview } = node;

            return (
              <Article key={id}>
                <Frame image={preview.source} />
                <Link to={slug}>
                  <Title>
                    {title}
                  </Title>
                </Link>
                <Content>
                  {content.replace(/<[^>]*>/g, '').slice(0, 150)}
                </Content>
              </Article>
            );
          })
      }
    </>
  );
};

List.propTypes = {
  posts: arrayOf(
    shape({
      node: shape({
        id: string.isRequired,
        title: string.isRequired,
        content: string.isRequired,
        categories: arrayOf(
          shape({
            id: string.isRequired,
            name: string.isRequired,
          })
        ).isRequired,
      }).isRequired,
    })
  ),
};

List.defaultProps = {
  posts: [],
};

List.displayName = 'List';

export default List;
